<template>
	<bill-debt-success
		v-if="successfulSubmit"
		:success-msg="successData.msg"
		:bill-debt-fee="successData.billDebtFee"
		:bill-debt-title="successData.billDebtTitle"
		:divide-method="successData.divideMethod"
		:file-download-link="fileDownloadLink"
		:on-done="onDone"
	/>
	<div v-else class="d-flex flex-column float-content bill-debt-registry-page">
		<b-alert v-model="error" class="p-1" variant="danger">
			برای تقسیم قبض بر اساس متراژ ا،بتدا باید متراژ واحدهای باقیمانده را ثبت کنید.
		</b-alert>
		<div class="text-center mb-2">
			<div class="d-flex align-items-center justify-content-center mb-1">
				<b-avatar class="mr-1" size="40" variant="light-primary">
					<img
						:src="require('@/assets/icons/bill-icon.svg')"
						alt="bill-debt"
						draggable="false"
						width="30"
					/>
				</b-avatar>
				<h4 class="m-0">تقسیم قبض / فاکتور</h4>
			</div>
			<span>
				در این بخش می‌توانید مبلغ قبض یا فاکتور را به روش‌های مختلف بین ساکنین یا مالکین تقسیم کنید.
			</span>
		</div>
		<!-- space -->

		<b-card class="py-1 mb:10">
			<b-form @submit.prevent="submitForm">
				<b-form-group label-for="bill_debt_title" label="عنوان قبض">
					<custom-vue-select
						id="bill_debt_title"
						v-model="billForm.debt_category_id"
						:options="billData"
						based-on="title"
						target-key="value"
>
						<template #footer>
							<b-button
								block
								variant="outline-primary"
								class="without-outline mt-1"
								@click="addBillTitle()"
							>
								<add-icon />
								ثبت عنوان جدید
							</b-button>
						</template>
					</custom-vue-select>
				</b-form-group>
				<b-form-group label-for="bill_debt_amount" label="مبلغ قبض">
					<b-form-input
						id="bill_debt_amount"
						v-model="billForm.bill_fee"
						@input="separate"
						style="text-align:left; direction: ltr;"
						required
					/>
				</b-form-group>
				<b-form-group label-for="bill_debt_division_type" label="نحوه تقسیم">
					<custom-vue-select
						v-model="billForm.split_type_id"
						:options="divideMethodOptions"
						based-on="split_type"
						target-key="split_type_id"
						prevent-default-click-event
					>
						<template #option="{ item, click }">
							<div>
								<div class="d-flex align-items-center justify-content-between">
									<span @click="click">{{ item.split_type }}</span>
									<img
										alt="bill-debt"
										:src="require('@/assets/icons/question-mark.svg')"
										draggable="false"
										width="15"
										@click="item.isHide = !item.isHide"
									/>
								</div>
								<p v-show="!item.isHide" class="text-wrap option-description mt-1">
									{{ item.split_type_info }}
								</p>
							</div>
						</template>
					</custom-vue-select>
				</b-form-group>
				<p class="text-danger" v-if="footageCondition">برای انتخاب این گزینه، متراژ {{buildingStatus.empty_area_count}} واحد باقمیمانده را تکمیل کنید.</p>
				<b-form-group class="mt-3">
          <b-form-radio-group
            id=""
            v-model="selectedradio"
            name=""
            class="d-flex justify-content-between form-input"
            :options="optionsradio"
          />
        </b-form-group>

        <template v-if="selectedradio === 'specific'">

					<search-input
        v-model="searchInput"
        class="search-input"
				placeholder="جستجو"
        style="border: 1px solid #c8c8c8; border-radius: 5px"
      />

          <div
            v-for="(user, index) in filteredUsers"
            :key="index"
            class="d-flex align-items-center justify-content-between mb-2"
          >
            <div class="d-flex align-items-center">
              <b-form-checkbox v-model="user.isChecked" />

              <b-avatar
                class="mx-2"
                size="42"
                :variant="randomVariantGenerator()"
              >
                <feather-icon
                  size="25"
                  icon="UserIcon"
                />
              </b-avatar>

              <div class="d-inline">
                <h6 class="debtuser-name">
                  {{ user.name }}
                </h6>
                <small>{{ user.floor }}</small>
              </div>
            </div>

            <h5 class="debtuser-unit">
              {{ user.unit }}
            </h5>
          </div>
        </template>
				<b-form-group label-for="bill_debt_desc" label="توضیحات">
					<b-form-textarea
						id="bill_debt_desc"
						v-model="billForm.description"
						rows="2"
					/>
				</b-form-group>

				<b-button :disabled="footageCondition" type="submit" block variant="primary" class="mt-5">ثبت</b-button>
			</b-form>
		</b-card>
	</div>
</template>

<script>
import AddIcon from "@core/components/svg-icons/AddIcon.vue";
import {
	BButton,
	BCard,
	BFormGroup,
	BForm,
	BFormInput,
	BAvatar,
	BAlert,
	BFormRadioGroup,
	BFormCheckbox,
	BFormTextarea
} from "bootstrap-vue";
import CustomVueSelect from "@core/components/custom-vue-select/CustomVueSelect.vue";
import { randomVariantGenerator } from '@core/utils'
import SearchInput from "@/@core/components/search-input/SearchInput.vue";
import BillDebtSuccess from "./BillDebtSuccess.vue";
import managerService from "@/api/managerService";
import axios from "@/libs/axios";
import { separator } from "@core/helper";

export default {
	components: {
		BButton,
		BAvatar,
		BCard,
		BFormGroup,
		BForm,
		BFormInput,
		AddIcon,
		CustomVueSelect,
		BAlert,
		BillDebtSuccess,
		BFormRadioGroup,
		BFormCheckbox,
		BFormTextarea,
		SearchInput

	},
	data() {
		return {

			billForm: {
				debt_category_id: '',
				bill_fee: '',
				split_type_id: "",
				description: ''
			},
			titleOptions: ["قبض آب", "قبض برق", "قبض گاز", "قبض تلفن"],
			divideMethodOptions: [],
			successfulSubmit: false,
			successData: {
				msg: "",
				billDebtFee: "",
				billDebtTitle: "",
				divideMethod: "",
			},
			fileDownloadLink: null,
			error: false,
			selectedradio: "all",
      optionsradio: [
        { text: "برای همه واحد ها", value: "all" },
        { text: "برای واحد خاص", value: "specific" },
      ],
			usersData: [],
			billData: [],
			searchInput: '',
      token: '',
			buildingStatus: {},
		};
	},
	computed: {
		footageCondition() {
			return this.billForm.split_type_id && this.billForm.split_type_id.targetValue === 3 && this.buildingStatus.empty_area_count === 0
		},
		filteredUsers() {
      if (this.searchInput !== "") {
        return this.usersData.filter(
          (item) => item.name.includes(this.searchInput)
            || item.unit.includes(this.searchInput)
        );
      }
      return this.usersData;
    },
	},
	created() {
		this.getBuildingData()
		this.getDebtSplitType();
	},
  mounted() {
    this.token = localStorage.getItem('token')
  },
  methods: {
		separate() {
			this.billForm.bill_fee = separator(this.billForm.bill_fee)
		},
		async getBuildingData() {
			const { data } = await axios.get("manager/debt/formula/status", {
				headers: {
					'Authorization': `Bearer ${localStorage.getItem('token')}`
				}
			});
			this.buildingStatus =  data.building_status
		},
		randomVariantGenerator,
		async getDebtSplitType() {
			try {
				const userData = await managerService.unit()
				const BillData = await managerService.commonBill()
				Object.entries(BillData.data.list).forEach(item => {
					this.billData.push({
						title: item[1],
						value: item[0]
					})
				})
				this.usersData = userData.data.list.map((item) => ({
					unitId: item.unit_id,
					name: item.resident.name,
					floor: item.floor.floor_name,
					unit: item.unit_name,
					isChecked: false,
				}));

				const  splitTypeData  = await managerService.commonSplitType()
				if (splitTypeData.data?.status !== "success") throw new Error("Unsuccessful Response");
				const splitTypeList = splitTypeData.data.list;
				this.divideMethodOptions = splitTypeList.map((item) => ({ ...item, isHide: true }));
			} catch (error) {
				this.error = true;
			}
		},
		addBillTitle() {
			this.$router.push({ name: "bill-registration", query: {back: this.$router.currentRoute.name}})
		},
		onDone() {
			this.$router.push({ name: "billDebtRegistration" });
		},
		async submitForm() {
			this.successfulSubmit = false;
			try {
				const unitIds = this.usersData
					.filter((item) => item.isChecked === true)
					.map((item) => [+item.unitId]);
				const submitResponse = await managerService.debtStoreBill(
					{
						debt_category_id: this.billForm.debt_category_id.targetValue,
						bill_fee: +this.billForm.bill_fee,
						split_type_id: this.billForm.split_type_id.targetValue,
						description: this.billForm.description,
						debtor: [...unitIds.flat()]
					},
					{
						headers: {
							'Authorization': 'Bearer ' + localStorage.getItem('token'),
						},
					}
				);

				if (submitResponse.data?.status !== "success") throw new Error("Unsuccessful Response");

				// console.log(submitResponse);

				this.fileDownloadLink = submitResponse.data.download_link
				this.successData.msg = submitResponse.data.message
				this.successData.billDebtFee = submitResponse.data.bill_fee
				this.successData.billDebtTitle = submitResponse.data.bill_title
				this.successData.divideMethod = submitResponse.data.share_type
				// this.successData = [
				// 	{
				// 		title: "مبلغ قبض",
				// 		data: submitResponse.bill_fee,
				// 	},
				// 	{
				// 		title: "عنوان قبض",
				// 		data: submitResponse.bill_title,
				// 	},
				// 	{
				// 		title: "نحوه تقسیم",
				// 		data: submitResponse.share_type,
				// 	},
				// ];

				this.successfulSubmit = true;
			} catch (error) {
				this.error = true;
			}
		},
	}
};
</script>

